html{
  font-size: 24px;
  line-height: 2;
 
}
.wrap{
  display: flex;
  justify-content: space-between;
}
header{
  position: fixed;
  display: block;
  width:100%; height:10vh;
  background-color: #ff5f00;
  display: flex;
  align-items: center;
  justify-content: center;
  nav{
    margin: 20px;
    display: flex;
    a{
      color: #ffffff;
      margin: 10px 30px;
      display: block;
    }
  }
}
main{
  width:100%;
  height: 100vh;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  >div{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.product{
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 30px 0;
  
}
.content{
  display: flex;
  flex-direction: column;
  >p{
    width: 300px;
    height: 300px;
    >img{
      width: 100%;
      height: 100%;
      border-radius: 150px;
      object-fit: cover;
    }
  }
  >div{
    text-align: center;
  }
}

.row{
  display: flex;
  flex-direction: row;
  gap: 50px;
}